.text div{

    background-color: #1f1f20;

    color: #ffffff;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
}

.text h1 {
    position: relative;
    display: inline-block;
    letter-spacing: 5px;
    margin: 0;
    padding: 10px 0 10px;
    font: 300 40px/1.2 'Josefin Sans', sans-serif;
}

.text span {
    text-align: left;
    font-stretch: normal;
    display: table;
    font: 300 14px/1.8 'Josefin Sans', sans-serif;
}

.text p {
    font: 300 40px/1.2 'Josefin Sans', sans-serif;
    font-size: 27px;
    display: block;
    text-align: left;
    line-height: 1.8;
    margin-top: 0;

}

.photo img {
    width: 345px;
    height: 383px;

}

